<template>
    <div >

        <table cellpadding="0" cellspacing="1" border="1" class="smart-table" width="100%">


        <tr>
            <td width="10%" align="right">订单编号:</td>
            <td width="*">
                {{shouhou.orderno}}
            </td>
        </tr>


        <tr>
            <td width="10%" align="right">商品名:</td>
            <td width="*">
                {{shouhou.spname}}
            </td>
        </tr>



        <tr>
            <td width="10%" align="right">数量:</td>
            <td width="*">
                {{shouhou.count}}
            </td>
        </tr>


        <tr>
            <td width="10%" align="right">价格(元):</td>
            <td width="*">
                {{shouhou.price}}¥
            </td>
        </tr>


        <tr>
            <td width="10%" align="right">总价(元):</td>
            <td width="*">
                {{shouhou.totalfee}}¥
            </td>
        </tr>


        <tr>
            <td width="10%" align="right">图片:</td>
            <td width="*">
                <img width="120" height="120" :src="hostHead+shouhou.tupian"/>

            </td>
        </tr>


        <tr>
            <td width="10%" align="right">状态:</td>
            <td width="*">
                <span v-if="shouhou.state==1">等待处理</span>
                <span v-if="shouhou.state==2">同意</span>
                <span v-if="shouhou.state==3">拒绝</span>

            </td>
        </tr>


        <tr>
            <td align="right">创建时间:</td>
            <td>
                {{shouhou.createtime}}
            </td>
        </tr>


        <tr>
            <td width="10%" align="right">原因:</td>
            <td width="*">
                {{shouhou.reason}}
            </td>
        </tr>



        <tr>
            <td align="right">退货描述:</td>
            <td>
                <div v-html="shouhou.tdes"></div>
            </td>
        </tr>


    </table>
    </div>
</template>

<script>
    import {VueUtil} from "../util/vueutil";

    export default {
        name: "shouhoudetails",
        data() {
            return {
                actiontype: 'save',
                hostHead: this.$app.contextPath,
                "shouhou": {},
            }
        },
        props:{
          params:{
              type:Object
          }
        },
        methods: {
            //begin load
            async load() {
                let id = this.params.id;
                let util = new VueUtil(this);
                if (id != null) {
                    let url = "admin/shouhou/load";
                    let {data: res} = await util.http.post(url, {id: id});
                    console.log("res", res);
                    if (res != null && res.data != null)
                        this.shouhou = res.data;
                }
            },//end load

        },
        created() {
            this.load();
        }
    }
</script>

<style scoped>

</style>
